@import "../common/mixins";
@import "../common/variable";

.contact-find {
  padding: r(100) 0;
  @include max-tablet {
    padding: r(50) 0;
  }
  &__container {}
  &__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    grid-gap: r(20);
    @include max-laptop {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      grid-gap: r(35);
    }
  }
  &__main {
    width: 45%;
    padding-inline: r(100);
    @include max-laptop {
      padding: 0;
    }
    @include max-laptop {
      width: 100%;
    }
  }
  &__title {
    color: $colorOrange;
  }
  &__list {
    margin-top: r(35);
  }
  &__map {
    width: 55%;
    @include max-laptop {
      width: 100%;
    }
    picture {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      font-size: 0;
    }
    img {
      width: 100%;
    }
  }
}

.find-list {
  display: grid;
  grid-row-gap: r(20);
  list-style: none;
  li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    grid-gap: r(20);
    svg {
      -ms-flex-negative: 0;
          flex-shrink: 0;
      width: r(20);
      height: r(20);
      color: $colorYellow;
    }
    a {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}